import { produce } from 'immer';
import { History } from 'history';

import { Reducer, initialState, AppAction } from '../constants';

import { getRouter } from './router';
import { cart } from './cart';
import { products } from './products';
import { user } from './user';

export const getRootReducer = (history: History) => {
    const router = getRouter(history);

    return (state = initialState, action: AppAction) =>
        produce(state, (draft) => {
            [router, cart, products, user].forEach((reducer: Reducer) => reducer(draft, action));
        });
};
