import { Reducer, AppState, initialState } from '../constants';

// TODO:       
const getTotalCount = (cart: AppState['cart']) =>
    Object.values(cart.itemsMap).reduce((acc, item) => {
        acc += item.count;

        return acc;
    }, 0);

// TODO:       
const getTotalPrice = (cart: AppState['cart'], products: AppState['products']) =>
    cart.order.reduce((acc, itemId) => {
        const productItem = products.itemsMap[itemId];
        const cartItem = cart.itemsMap[itemId];

        acc += cartItem.count * Number(productItem.price);

        return acc;
    }, 0);

export const cart: Reducer = (store, action) => {
    const { cart, products } = store;

    switch (action.type) {
        case 'ADD_ITEM': {
            const { count: countString, item: itemId } = action.payload;
            const count = Number(countString);
            const productItem = products.itemsMap[itemId];

            if (!productItem) {
                return;
            }

            const cartItem = cart.itemsMap[itemId];

            if (cartItem) {
                const maxCount = Number(productItem.quantity_left);
                const expectedCount = cartItem.count + count;
                const countToAdd = expectedCount <= maxCount ? count : count - (expectedCount - maxCount);

                cartItem.count += countToAdd;
            } else {
                cart.itemsMap[itemId] = {
                    itemId,
                    count,
                };

                cart.order.push(itemId);
            }

            cart.totalCount = getTotalCount(cart);
            cart.totalPrice = getTotalPrice(cart, products);

            break;
        }

        case 'REMOVE_ITEM': {
            const { item: itemId, count: countString, shouldRemoveMention } = action.payload;
            const count = Number(countString);

            const cartItem = cart.itemsMap[itemId];
            const productItem = products.itemsMap[itemId];

            if (!cartItem || !productItem) {
                return;
            }

            cartItem.count = Math.max(0, cartItem.count - count);

            cart.totalCount = getTotalCount(cart);
            cart.totalPrice = getTotalPrice(cart, products);

            if (!cartItem.count && shouldRemoveMention) {
                delete cart.itemsMap[itemId];
                cart.order = cart.order.filter((cartItemId) => cartItemId !== itemId);
            }

            break;
        }

        case 'REMOVE_ALL': {
            store.cart = initialState.cart;

            break;
        }

        case 'GO_TO_CATALOG': {
            if (action.payload.clear) {
                store.cart = initialState.cart;
            }

            break;
        }

        default:
            break;
    }
};
