import React from 'react';
import { splitByThousands } from 'tools';
import { useDispatch, useSelector } from 'hooks';
import { REMOVE_ITEM, ADD_ITEM } from 'store/actions';
import { SHOW_ITEMS_MOCK } from 'mocks';
import withBem from 'hoc/withBem';

import { CartListItem } from '../ListItem/CartListItem';
import { MINIMAL_PRICE } from '../Cart';

import './CartList.scss';

type CartListProps = BemProps;

const ITEM_WIDTH = 152;

const onScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
};

const getOffset = (index: number, itemsCount: number) => {
    //      0,      
    //    ( = 1),      
    // 3- ,     1
    let relatedIndex = Math.max(0, index - 1);

    //    
    relatedIndex = Math.min(Math.max(0, itemsCount - 4), relatedIndex);

    //      
    return relatedIndex * ITEM_WIDTH;
};

const Component: React.FC<CartListProps> = ({ bem }) => {
    const [selectedItemIndex, setSelectedItemIndex] = React.useState(0);
    const dispatch = useDispatch();
    const { cart, products } = useSelector(({ cart, products }) => ({ cart, products }));
    const totalPriceInRubles = Math.ceil(cart.totalPrice / 100);
    const cartItemsCount = cart.order.length;
    const isFirstItemSelected = !selectedItemIndex;
    const isLastItemSelected = selectedItemIndex === cartItemsCount - 1;

    const wrapperStyle: React.CSSProperties = {
        transform: `translateY(-${getOffset(selectedItemIndex, cartItemsCount)}px)`,
    };

    React.useEffect(() => {
        if (process.env.REACT_APP_ENVIRONMENT === 'development') {
            const stock = {
                ...SHOW_ITEMS_MOCK,
                ...Object.values(SHOW_ITEMS_MOCK).reduce((acc, item) => {
                    const guid = item.guid + Math.random() * 1000;

                    return {
                        ...acc,
                        [guid]: {
                            ...item,
                            guid,
                        },
                    };
                }, {}),
            };

            window.AssistantClient.onData({
                type: 'smart_app_data',
                // eslint-disable-next-line
                smart_app_data: {
                    command: 'SHOW_ITEMS',
                    stock,
                },
            });

            Object.values(stock).map((item) =>
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line
                    smart_app_data: {
                        command: 'ADD_ITEM',
                        item: item.guid,
                        count: String(Math.floor(Math.random() * 5)),
                    },
                }),
            );
        }
    }, []);

    return (
        <div className={bem()}>
            <div className={bem('container')} onScroll={onScroll}>
                <div
                    className={bem.modify(
                        { top: true, hidden: cartItemsCount <= 4 || isFirstItemSelected },
                        'gradient',
                    )}
                />
                <div className={bem('wrapper')} style={wrapperStyle}>
                    {cart.order.map((itemId, index) => {
                        const productItem = products.itemsMap[itemId];
                        const cartItem = cart.itemsMap[itemId];

                        return (
                            <CartListItem
                                {...productItem}
                                onSelect={setSelectedItemIndex}
                                onLessClick={(count: number, shouldRemoveMention: boolean) =>
                                    dispatch(
                                        REMOVE_ITEM({
                                            item: itemId,
                                            count: String(count),
                                            shouldRemoveMention,
                                        }),
                                    )
                                }
                                onMoreClick={() =>
                                    dispatch(
                                        ADD_ITEM({
                                            item: itemId,
                                            count: '1',
                                        }),
                                    )
                                }
                                count={cartItem.count}
                                index={index + 1}
                                key={itemId}
                            />
                        );
                    })}
                </div>
                <div
                    className={bem.modify(
                        { bottom: true, hidden: cartItemsCount <= 4 || isLastItemSelected },
                        'gradient',
                    )}
                />
            </div>
            <div className={bem('total-price')}>  {splitByThousands(String(totalPriceInRubles))} </div>
            {cart.totalPrice < MINIMAL_PRICE ? (
                <div className={bem('minimal-price')}>
                        {Math.ceil(MINIMAL_PRICE - totalPriceInRubles)} 
                </div>
            ) : (
                ''
            )}
        </div>
    );
};

export const CartList = withBem(Component, 'CartList');
