import { createAction, createEmptyAction } from 'tools/action-creator';

import {
    ShowItemsCommandPayload,
    AddItemCommandPayload,
    RemoveItemCommandPayload,
    RemoveAllCommandPayload,
    GoToCatalogCommandPayload,
    ValidationOkCommandPayload,
} from '../constants/native';

export const SHOW_ITEMS = (payload: ShowItemsCommandPayload) => createAction('SHOW_ITEMS', payload);

export const ADD_ITEM = (payload: AddItemCommandPayload) => createAction('ADD_ITEM', payload);

interface RemoveItemCommandPayloadExtended extends RemoveItemCommandPayload {
    shouldRemoveMention?: boolean;
}
export const REMOVE_ITEM = (payload: RemoveItemCommandPayloadExtended) => createAction('REMOVE_ITEM', payload);

export const REMOVE_ALL = (payload: RemoveAllCommandPayload) => createEmptyAction('REMOVE_ALL');

export const GO_TO_CATALOG = (payload: GoToCatalogCommandPayload) => createAction('GO_TO_CATALOG', payload);

export const VALIDATION_OK = (payload: ValidationOkCommandPayload) => createAction('VALIDATION_OK', payload);
