import { LocationState } from 'history';
import { Dispatch } from 'redux';
import { RouterState, RouterAction } from 'connected-react-router';

import * as actions from '../actions';

import { ProductItem } from './products';

export const initialState = {
    cart: {
        itemsMap: {} as Record<string, CartItem>,
        order: [] as string[],
        totalCount: 0,
        // discount value?
        totalPrice: 0, //  
        orderId: 0,
    },
    products: {
        itemsMap: {} as Record<string, ProductItem>,
    },
    //    undefined,     
    router: (undefined as unknown) as RouterState<LocationState>,
    user: {
        address: '',
        location: '',
        phoneNumber: '',
        name: '',
    },
};

export type AppState = typeof initialState;

export type ActionTypes = typeof actions;
export type BaseAction = ReturnType<ActionTypes[keyof ActionTypes]>;
export type AppAction = BaseAction | RouterAction<LocationState>;
export type AppDispatch = Dispatch<AppAction>;
export type Reducer<T = AppAction> = (state: AppState, action: T) => void;

export interface CartItem {
    itemId: string;
    count: number;
}
