import React from 'react';
import withBem from 'hoc/withBem';
import { ProductItem } from 'store/constants/products';
import { ADD_ITEM } from 'store/actions/native';
import { useFocusOnMount, useDispatch, useSelector } from 'hooks';

import { CatalogListItem } from '../ListItem/CatalogListItem';
import './CatalogList.scss';

type CatalogListProps = BemProps;

const Component: React.FC<CatalogListProps> = ({ bem }) => {
    const dispatch = useDispatch();
    const {
        cart,
        products: { itemsMap },
    } = useSelector(({ cart, products }) => ({ cart, products }));
    const mountFocusRef = useFocusOnMount<HTMLDivElement>();

    return (
        <div className={bem()}>
            <div className={bem('container')}>
                {Object.values(itemsMap).map((item: ProductItem, index) => (
                    <CatalogListItem
                        {...item}
                        mountRef={!index ? mountFocusRef : undefined}
                        countInCart={cart.itemsMap[item.guid] ? cart.itemsMap[item.guid].count : 0}
                        key={item.guid}
                        index={index + 1}
                        onClick={() => dispatch(ADD_ITEM({ item: item.guid, count: '1' }))}
                    />
                ))}
            </div>
        </div>
    );
};

export const CatalogList = withBem(Component, 'CatalogList');
